body {
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 400;
  line-height: 1.5;
  text-align: justify;
  text-justify: inter-word;
}

.heading-primary {
  color: $color-white;
  text-transform: uppercase;

  &--main {
    display: block;
    font-size: 4rem;
    font-weight: 400;
    letter-spacing: 1rem;

    @include respond(phone) {
      letter-spacing: 0.5;
      font-size: 6rem;
    }
  }

  &--sub {
    display: block;
    font-size: 1.6rem;
    font-weight: 700;
    letter-spacing: 1rem;
    @include respond(phone) {
      letter-spacing: 0.5;
      font-size: 2.5rem;
    }
  }
}

.heading-secondary {
  font-size: 4.5rem;
  text-transform: uppercase;
  font-weight: 700;
  display: inline-block;
  background-image: linear-gradient(
    to right,
    $color-primary-light,
    $color-primary-dark
  );
  -webkit-background-clip: text;
  color: transparent;
  letter-spacing: 0.2rem;
  transition: all 0.2s;

  &:hover {
    transform: skewY(2deg) skewX(15deg) scale(1.1);
    text-shadow: 0.5rem 1rem 2rem rgba($color-black, 0.2);
  }

  @include respond(phone) {
    font-size: 5rem;
  }
}

.heading-tertiary {
  font-size: $default-font-size;
  font-weight: 700;
  text-transform: uppercase;

  @include respond(phone) {
    font-size: 2.2rem;
  }
}

.paragraph {
  font-size: $default-font-size;

  @include respond(phone) {
    font-size: 1.8rem;
  }
}
